import React,{useState,useEffect} from "react";
import { Link, useLocation} from "react-router-dom";
import { toast } from "react-toastify";
import fireDb from "../firebase";
import "./Home.css";
import "./AddEmployee";
import "../components/Header.js"


const Home = () => {
    const[activeTab ,setActiveTab] = useState("Home");
    const location = useLocation();
    useEffect(() => {
        // if (location.pathname === "/") {
        //   setActiveTab("Home");
        // } 
        // // else if (location.pathname === "/add") {
        // //   setActiveTab("AddEmployee");
         
        // } 
         if (location.pathname === "/about") {
          setActiveTab("About");
        }
      }, [location]);






    const [data,setData] =useState({});
// snapshot the data from db to UI
    useEffect(()=> {
        fireDb.child("EmployeeDetials").on("value", (snapshot) =>{
            if(snapshot.val()!==null){
                setData({...snapshot.val() });
            }else{
                setData({});

            }
        });
        return() => {
            setData({});
        };
    }, []);

    // delete btn function
    const onDelete = (id) =>{
        if(window.confirm("Are you sure want to delete this?")){
            fireDb.child(`EmployeeDetials/${id}`).remove((err)=>{
                if(err){
                    toast.error(err);

                }else{
                    toast.success("Details Deleted succesfully!");
                }
            });

        }
    };

    // fetch data from db to table 
    return (
        <div className="maindiv">
        <div style={{marginTop:"150px"}}>
           <table className="styled-table">
           <thead>
         <tr><div className="container"><th style={{textAlign:"center"}}>Manage Employee Details</th></div>
         <td     style={{textAlign:"left"}}  className="addemp"></td>
         <td     style={{textAlign:"left"}}  className="addemp"></td>
         <td     style={{textAlign:"left"}}  className="addemp"></td>
         <td     style={{textAlign:"left"}}  className="addemp"></td>
         <td     style={{textAlign:"left"}}  className="addemp"></td>
         <td     style={{textAlign:"left"}}  className="addemp"></td>
         <td     style={{textAlign:"right"}}  className="addemp"> 
         <Link to="/add">
        <button className={`${activeTab ==="AddEmployee" ? "active" : ""}`," btn btn-add"} onClick ={()=> setActiveTab("AddEmployee")}><i class="fa fa-plus" aria-hidden="true"></i> Add</button>
        </Link></td>
         </tr>
           <tr>
           <th style={{textAlign:"center"}} className="thh">Sno</th>
           <th style={{textAlign:"left"}}>Name</th>
           <th style={{textAlign:"left"}}>EmpID</th>
           <th style={{textAlign:"left"}}>EmailID</th>
           <th style={{textAlign:"Center"}}>EntityID</th>
           <th style={{textAlign:"left"}}>CorporateName</th>
           <th style={{textAlign:"left"}}>Mobileno</th>
           <th style={{textAlign:"center"}}>Action</th>
           </tr>
           </thead>
           <tbody>
               {Object.keys(data).map((id, index) => {
                   return(
                       <tr key={id}>
                           <th scope="row" >{index+1}</th>
                           <td style={{textAlign:"left"}}>{data[id].name}</td>
                           <td style={{textAlign:"left"}}>{data[id].empid}</td>
                           <td style={{textAlign:"left"}}>{data[id].primaryemailid}</td>
                           <td style={{textAlign:"center"}}>{data[id].entityid}</td>
                           <td style={{textAlign:"left"}}>{data[id].corporatename}</td>
                           <td style={{textAlign:"right"}}>{data[id].mobileno}</td>
                           <td>

                           {/* button link to update view and delete pages */}
                           <Link to={`/view/${id}`}>
                                   <button className="btn btn-view"><i class="fa fa-eye"></i> View</button>
                               </Link>
                               <Link to={`/update/${id}`}>
                                   <button className="btn btn-edit"><i class="fa fa-pencil-square-o" aria-hidden="true"></i> Edit</button>
                               </Link>
                               <button className="btn btn-delete"onClick={()=>onDelete(id)}><i class="fa fa-trash" aria-hidden="true"></i> Delete</button>
                           </td>
                       </tr>
                   );
               }
              )}
           </tbody>

           </table>
        </div>
        </div>
    );
};

export default Home;
